<template>
  <div id="kaleidoscope">
    <canvas ref="myCanvas"></canvas>
  </div>
</template>

<script>
export default {
  name: 'kaleidoscope',
  data () {
    return {
      i: 10000
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let canvas = this.$refs.myCanvas,
        context = canvas.getContext('2d'),
        time = 0,
        w = 1,
        h = 1,
        cos = Math.cos,
        sin = Math.sin,
        PI = Math.PI,
        self = this

      function resize () {
        canvas.width = w = innerWidth
        canvas.height = h = innerHeight
      }

      // 监听尺寸
      addEventListener('resize', resize, false)

      resize()

      setInterval(function () {
        context.clearRect(0, 0, w, h)
        context.fillStyle = 'rgba(255,255,0,.5)'
        context.globalCompositeOperation = 'lighter'
        time += 0.1
        self.i = 10000
        while (self.i--) {
          let r = ((w + h) * 0.4) * (cos((time + self.i) * (0.05 + ((sin(time * 0.00002) / PI) * 0.2))) / PI)
          context.fillRect(sin(self.i) * r + (w / 2), cos(self.i) * r + (h / 2), 1, 1)
        }
      }, 16)
    }
  }
}
</script>

<style scoped>
#kaleidoscope {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #333;
  overflow: hidden;
}
</style>
